body, p, a, a:hover {
    padding: 0;
    margin: 0;
    font-family: Impact, Charcoal, sans-serif;
    text-decoration: none;
    text-underline: none;
    color: inherit;
}

header {
    font-size: 2rem;
    padding: 10px;
    min-height: 3em;
    line-height: 3em;
    background-color: cornflowerblue;
    color: floralwhite;
    text-transform: uppercase;
}

main {
    padding: 10px;
}

.basic-spacing {
    margin: 15px;
    padding: 15px;
}

.last-clear td:last-child > span {
    visibility: hidden;
}

.last-clear td:last-child {
    border: none;
    background-color: white;
    min-width: 150px;
    width: 150px;
}

.show-on-hover > tbody > tr:hover > td > span {
    visibility: visible;
}

.last-clear td:last-child:hover {
    visibility: visible;
}